<template>
  <div class="readnow" :style="{backgroundColor:thisbgColor}">
    <!-- 上导航栏 -->
    <div
      class="readnow-nav-box"
      :style="{backgroundColor:thisbgColor,color:colors}"
      v-show="getshow"
    >
      <van-nav-bar
        :class="{sunday:!imgActive}"
        :title="detilData.title"
        left-arrow
        @click-left="back"
        :style="{color:colors}"
      />
    </div>
    <!-- 章节内容 -->
    <div class="readnow-box" @click="SetShows" ref="newScroll">
      <div class="readnow-content">
        <div class="readnow-title" :style="{color:colors}">{{detilData.title}}</div>
        <div>
          <p
            class="readnow-text"
            v-for="(item,index) in linkData"
            :key="index"
            :style="{lineHeight:thisLineHeight,fontSize:thisSize + 'px',color:colors}"
          >{{item}}</p>
        </div>
        <div class="read-end"><van-divider :style="{ color: colors, borderColor: '#bbb', padding: '0 16px' }"  @click="getNext"  @click.capture.stop>已经到达底部</van-divider></div>
      </div>
    </div>
    <!-- 下导航栏 -->
    <div v-show="getshow" class="footer" :style="{backgroundColor:thisbgColor}">
      <div class="footer-top" :style="{color:colors}">
        <div class="footer-top-txt" @click="getprev">上一章</div>|
        <div class="footer-top-txt" @click="getNext">下一章</div>
      </div>
      <div class="footer-bottom">
        <div class="footer-box" :class="{monday:!imgActive}" @click="back">
          <div class="iconfont icon-mulu"></div>
          <div class="footer-title">目录</div>
        </div>
        <div class="footer-box" :class="{monday:!imgActive}" @click="showoverlay">
          <div
            class="iconfont"
            :class="imgActive ? 'icon-yueliang1':'icon-taiyangbaitianrijianmoshibaitianqingxianxing'"
          ></div>
          <div class="footer-title">
            <div>{{imgActive?'夜间':'白天'}}</div>

            <van-overlay :show="show">
              <div class="wrapper" @click.stop>
                <div class="block">
                  <img v-show="!imgActive" class="icon" src="../assets/image/moons.gif" />
                  <img v-show="imgActive" class="icon" src="../assets/image/summer.gif" alt />
                </div>
              </div>
            </van-overlay>
          </div>
        </div>
        <div class="footer-box" :class="{monday:!imgActive}">
          <div class="iconfont icon-aa"></div>
          <div class="footer-title" @click="setShow">设置</div>
        </div>
        <div class="footer-box" :class="{monday:!imgActive}" @click="gotoPage('Bookmall')">
          <div class="iconfont icon-shu"></div>
          <div class="footer-title">免费小说</div>
        </div>
      </div>
    </div>
    <!-- 字体背景面板 -->
    <div v-show="setActive" class="readnow-box-setting" :style="{backgroundColor:thisbgColor}">
      <van-cell is-link @click="gotoPage('My')">
        <template #title>
          <span class="custom-title clearfix">
            <div class="my-head-img fl">
              <img class="auto-img" src="../assets/image/chichigua.png" alt />
            </div>
            <div class="fl my-person" :style="{color:colors}">个人中心</div>
          </span>
        </template>
      </van-cell>
      <div class="readnow-item">
        <div class="clearfix">
          <div class="fl readnow-item-title">间距</div>
          <div class="fl readnow-item-right">
            <div
              class="items-icon iconfont"
              v-for="(item,index) in sizeData"
              :key="index"
              :class="item.icon"
              :style="{color:(item.isActive ? '#75A8E5':'#D3D3D3')}"
              @click="sizeChange(item)"
            ></div>
          </div>
          <div class="clearfix">
            <div class="fl readnow-item-title">字号</div>
            <div class="clearfix fl readnow-item-right">
              <div class="fl readnow-fontsize" @click="reduce">A-</div>
              <div class="fl readnow-fontsize" @click="add">A+</div>
            </div>
          </div>
          <div class="clearfix">
            <div class="fl readnow-item-title">背景</div>
            <div class="readnow-item-right fl">
              <div
                class="readnow-item-bgcolor"
                v-for="(item,index) in bgColorData"
                :key="index"
                :class="item.isActive ? 'active' : ''"
                :style="{backgroundColor:item.color}"
                @click="bgColor(item)"
              ></div>
              <div class="readnow-item-bgcolor"></div>
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import "../assets/css/readnow.less";
export default {
  data() {
    return {
      detilData: {},
      linkData: [],
      // 上下导航显示隐藏
      getshow: false,
      // 设置字体颜色面板显示隐藏
      setActive: false,
      // 日夜切换
      imgActive: true,
      // 切换背景颜色数据
      bgColorData: [
        {
          color: "#FAFAF9",
          isActive: false,
        },
        {
          color: "#E7E0CC",
          isActive: true,
        },
        {
          color: "#BDCDBE",
          isActive: false,
        },
        {
          color: "#D9D8D7",
          isActive: false,
        },
        {
          color: "#F1E8E8",
          isActive: false,
        },
        {
          color: "#D2E6E8",
          isActive: false,
        },
      ],
      // 切换行间距数据
      sizeData: [
        {
          icon: " icon-jianju",
          lineHeight: "50px",
          isActive: false,
        },
        {
          icon: " icon-jianju2",
          lineHeight: "40px",
          isActive: false,
        },
        {
          icon: " icon-jianju1",
          lineHeight: "30px",
          isActive: true,
        },
      ],
      //行高
      thisLineHeight: "30px",
      // 背景颜色
      thisbgColor: "#E7E0CC",
      //初始背景颜色
      currentbgColor: "#E7E0CC",
      // 字体大小
      thisSize: 16,
      // 字体颜色
      colors: "#323233",
      //初始字体颜色
      currentColors: "#323233",
      show: false,
      //存储章节的key值
      key: 0,
      //所有章节
      allSection: [],
      // 章节链接
      link:'',
      // 章节标题
      title:''
    };
  },
  created() {
    this.key = this.$route.query.index;
    let url = this.$route.query.url;
    this.getLinkBooks(url);
    this.pushSection();
    
  },
  beforeRouteLeave(to, from, next) {
    for (let i = 0; i < this.allSection.length; i++) {
      if (i == this.key) {
        //存放当前章节链接
        this.link = this.allSection[i].link;
        // 存放当前章节名称
        this.title = this.allSection[i].title;
        break;
      }
    }

    let readObj = this.$store.state.readHistory;
    for (let i = 0; i < readObj.length; i++) {
      if (readObj[i].bookId == this.$route.query.id) {
        //找到相同id的书籍删除
        readObj.splice(i, 1);
        break;
      }
    }
    //存储书籍id 章节id
    this.$store.state.readHistory = readObj;
    let obj = { bookId: this.$route.query.id, chapterId: this.link, chapterTitle:this.title,index:this.key,switch:true};
    this.$store.dispatch("setReadHistory", obj);
    next();
  },
  methods: {
    //设置滚轮距顶部的距离
    setScroll(){
      this.$refs['newScroll'].scrollTop = 0;
    },
    //返回
    back() {
      this.$router.go(-1);
    },
    //点击激活上下导航栏
    SetShows() {
      if (this.getshow && this.setActive == false) {
        this.getshow = false;
        return;
      }
      if (this.setActive) {
        this.setActive = false;
        return;
      }
      this.getshow = true;
    },
    // 激活设置字体样式面包
    setShow() {
      this.setActive = !this.setActive;
    },
    showoverlay() {
      this.imgActive = !this.imgActive;
      if (this.imgActive) {
        this.thisbgColor = this.currentbgColor;
        this.colors = this.currentColors;
      } else {
        (this.thisbgColor = "#323233"), (this.colors = "#666B72");
      }
      this.show = true;
      setTimeout(() => {
        this.show = false;
      }, 1000);
    },
    //背景颜色
    bgColor(item) {
      if (!this.imgActive) {
        this.imgActive = true;
        this.setActive = false;
        this.show = true;
        setTimeout(() => {
          this.show = false;
        }, 1000);
      }
      for (let i = 0; i < this.bgColorData.length; i++) {
        if (this.bgColorData[i].isActive) {
          this.bgColorData[i].isActive = false;
          break;
        }
      }
      item.isActive = true;
      this.thisbgColor = item.color;
      this.currentbgColor = item.color;
    },
    //行高
    sizeChange(item) {
      for (let i = 0; i < this.sizeData.length; i++) {
        if (this.sizeData[i].isActive) {
          this.sizeData[i].isActive = false;
          break;
        }
      }
      item.isActive = true;
      this.thisLineHeight = item.lineHeight;
    },
    //字体的变大变小
    reduce() {
      this.thisSize < 12 ? 12 : this.thisSize;
      if (this.thisSize == 12) {
        return;
      }
      this.thisSize -= 2;
    },
    add() {
      this.thisSize < 26 ? 26 : this.thisSize;
      if (this.thisSize == 26) {
        return;
      }
      this.thisSize += 2;
    },

    //路由跳转
    gotoPage(name) {
      this.$router.push({ name: name });
    },
    //获取章节详情
    getLinkBooks(link) {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/chapters/" + encodeURIComponent(link),
      })
        .then((result) => {
          this.$toast.clear();
          if (result.status == 200) {
            this.detilData = result.data.chapter;
            this.linkData = result.data.chapter.cpContent.split("\r\n");
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    //获取vuex存储的数据
    pushSection() {
      this.allSection = this.$store.state.readNowData;
    },

    // 上一章
    getprev() {
      if (this.key == 0) {
        return;
      }

      let key = this.key - 1;
      for (let i = 0; i < this.allSection.length; i++) {
        if (i == key) {
          this.key = i;
          let ids = this.allSection[i].link;

          this.getLinkBooks(ids);
        }
      }
      this.setScroll();
    },

    //下一章
    getNext() {
      if (this.key == this.allSection.length-1) {
        return;
      }
      this.key = this.key + 1;
      for (let i = 0; i < this.allSection.length; i++) {
        if (i == this.key) {
          // this.key = i;
          let ids = this.allSection[i].link;

          this.getLinkBooks(ids);
        }
      }
      this.setScroll();
    },
  },
};
</script>

<style lang="less" scoped>
</style>